<template>
    <div class="app">
        <div class="incomeBox">
            <div class="titleInfo">
                <div class="bottomBox ov">
                    <div class="fl left"><p>总共支付升级费用</p><span>{{headData.expense}}</span></div>
                    <div class="fl right"><p>总共收取下级升级费用</p><span>{{headData.income}}</span></div>
                </div>
            </div>
            <div class="holderNum pr"><p>财务流水记录</p></div>
        </div>
        <van-list
                v-model="loading"
                :finished="finished"
                finished-text=""
                @load="getList"
        >
        <ul class="flowBox">
            <li v-for="item in listData"><a href="javascript:void(0);" class="ov">
                <p class="fl ov"><img class="fl" :src="item.userinfo.avatar" alt=""><em class="fl"><span>{{item.userinfo.nickname}}</span><i class="time">{{item.datetime}}</i></em></p>
                <p class="fr"><span>{{item.money}}</span><i>{{item.remark}}</i></p>
            </a></li>

        </ul>
        </van-list>

        <!--  公共组件  -->
       <tree-bar :tree_id="tree_id"></tree-bar>
    </div>

</template>

<script>
    import tabBar from '@/components/tabBar'
    import treeBar from '@/page/tree/treeBar'
    import { treeHead, treeLog } from '@/api/tree'
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    import { List } from "vant";
    import 'vant/lib/list/style';
    export default {
        name: "Treefinance",
        components: {
            tabBar,
            treeBar,
            [List.name]: List,
        },
        data()
        {
            return {
                tree_id:'',
                loading:false,
                finished:false,
                page:1,
                listData:[],
                headData:{
                    income:0,
                    expense:0
                }
            }
        },
        created()
        {
            this.tree_id = this.$route.query.treeId
        },
        mounted()
        {

        },
        methods:{

            // 我的财富树列表
            getList()
            {
                this.loading = true
                treeLog({page:this.page, tree_id:this.tree_id}).then(res => {
                    this.loading = false
                    if (this.page == 1)
                    {
                        this.headData = res.head;
                    }
                    this.page++
                    if (res.list.length < 20)
                    {
                        this.finished = true
                    }
                    this.listData = this.listData.concat(res.list)
                })
            }
        }
    }
</script>

<style scoped>
    @import "../../static/css/treefinance.css";
</style>



